const gallery = document.querySelector('.gallery');

function createGallery(arr) {
  return arr
    .map(
      ({ preview, original, description }) => `
    <li class="gallery-item">
       <a class="gallery-link" href="${original}" title="${description}">
       <img
        class="gallery-image"
        src="${preview}"
        alt="${description}"
      />
     </a>
    </li>
     `
    )
    .join('');
}

gallery.insertAdjacentHTML('beforeend', createGallery(images));
console.log(
  'Элементы для SimpleLightbox:',
  document.querySelectorAll('.gallery a')
);
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

let photo = new SimpleLightbox('.gallery a', {
  captions: true,
  captionSelector: 'self',
  captionData: 'title',
  captionType: 'attr',
  captionPosition: 'bottom',
  captionDelay: 250,
  captionHTML: true,
});

photo.on('show.simplelightbox', function () {
  console.log('photo is opend');
});

photo.on('error.simplelightbox', function (e) {
  console.log(e); // Some usefull information
});
